import React, { Component } from 'react';
import PropTypes from 'prop-types';

import './PriceBottom.scss';

class PriceBottom extends Component {
  render() {
    const {
      data,
    } = this.props;

    if (!data) {
      return null;
    }

    return (
      <div className="business-bottom-sticky-container">
        <div className="product-info">
          {data.productName && <h6>{data.productName}</h6>}
          {data.productInfo && <h3>{data.productInfo}</h3>}
        </div>
        <div className="price-info">
          {data.price && <h3>{data.price}</h3>}
        </div>
      </div>
    );
  }
}

PriceBottom.defaultProps = {
  data: null,
};

PriceBottom.propTypes = {
  data: PropTypes.object,
};

export default PriceBottom;
