import _ from 'lodash';
import React, { Component } from 'react';
import { Row, Col } from 'reactstrap';
import PropTypes from 'prop-types';

import FieldGenerator from 'components/FormsV3/FieldGenerator';
import { dateToStoreString } from 'lib/dateFunctions';

import './CheckoutFields.scss';

class CheckoutFields extends Component {
  renderDisplayField(item) {
    try {
      if (item.type === 'date' || item.type === 'end_date') {
        return dateToStoreString(item.value);
      }

      return item.value;
    } catch (error) {
      // eslint-disable-next-line
    }

    return 'N/A';
  }

  render() {
    const {
      inputFields,
      values,
      errors,
      onChangeValue,
      partialFlowStatus,
    } = this.props;

    if (!inputFields || !onChangeValue) {
      return null;
    }

    try {
      return _.map(inputFields, (field, index) => (
        <Col
          key={`checkout-field-${index}`}
          className="checkout-form-container"
          xs={12}
        >
          <Row noGutters>
            <Col xs={12}>{field.title && <p className="green-text">{field.title}</p>}</Col>
            {
              _.map(field.forms, (option, index2) => (
                <Row className="field-container" key={`field-${option.slug}-${index2}`} noGutters>
                  <Col className="name-container" md={6}>
                    <p>{option.title}</p>
                  </Col>
                  <Col className="field" md={6}>
                    {
                      option.readOnly || partialFlowStatus ?
                        <p className="dark-text">{this.renderDisplayField(values && values[option.slug])}</p>
                      :
                        <Row noGutters>
                          <FieldGenerator
                            values={values}
                            errors={errors}
                            fieldData={option}
                            onChangeValue={onChangeValue}
                            noInternalDesc
                          />
                        </Row>
                    }
                  </Col>
                </Row>
              ))
            }
            {index !== inputFields.length - 1 && <Col xs={12}><hr /></Col>}
          </Row>
        </Col>
      ));
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }
}

CheckoutFields.defaultProps = {
  partialFlowStatus: null,
};

CheckoutFields.propTypes = {
  inputFields: PropTypes.array.isRequired,
  values: PropTypes.object.isRequired,
  errors: PropTypes.object.isRequired,
  onChangeValue: PropTypes.func.isRequired,
  partialFlowStatus: PropTypes.string,
};

export default CheckoutFields;
