/**
 * CircleHeader.js
 * Circle component for claims
 *
 * @version 0.0.1
 * @author [Barry](barry@policypal.com)
 */
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import { Row, Col } from 'reactstrap';
import PropTypes from 'prop-types';

import { numberWithCommas } from 'lib/helperFunctions';

import Button from 'components/FormsV3/Button/Button';
import PromoCode from 'components/FormsV3/PromoCode/PromoCode';
import Checkbox from 'components/FormsV3/Checkbox/Checkbox';
import MoreCollapse from './MoreCollapse/MoreCollapse';

import './CheckoutPriceSummary.scss';

class CheckoutPriceSummary extends Component {
  constructor(props) {
    super(props);
    const { priceSummaryData } = this.props;

    const tnc = {};
    _.each(priceSummaryData.wordings, (wording, index) => {
      const name = `tnc_${index}`;
      tnc[name] = { type: 'checkbox', value: false };
    });

    this.state = {
      values: {
        ...tnc,
      },
      errors: {},
    };

    this.handleOnChange = this.handleOnChange.bind(this);
    this.handleOnNext = this.handleOnNext.bind(this);
  }

  async handleOnChange(type, name, value) {
    const { values, errors } = this.state;

    let newError = {};
    if (value) {
      newError = { [name]: null };
    }

    this.setState({
      values: {
        ...values,
        [name]: { type, value },
      },
      errors: { ...errors, ...newError },
    });
  }

  async handleOnNext() {
    const {
      isCheckout,
      onNext,
      postCheckout,
      promoCode,
      priceSummaryData,
    } = this.props;

    let callback = null;

    if (isCheckout) {
      const { values } = this.state;
      const newErrors = {};

      _.each(priceSummaryData.wordings, (wording, index) => {
        const name = `tnc_${index}`;

        if (values[name] && !values[name].value) {
          newErrors[name] = 'Please agree to this field!';
        }
      });

      if (!_.isEmpty(newErrors)) {
        this.setState({
          errors: newErrors,
        });
      } else {
        callback = () => postCheckout(promoCode, true);
      }
    }

    onNext(callback);
  }

  renderMain() {
    const { priceSummaryData } = this.props;

    if (!priceSummaryData.main) {
      return null;
    }

    return (
      <Col xs={12}>
        <h2 className="main">
          <span>{priceSummaryData.main.title}</span>
          <span>{priceSummaryData.main.value}</span>
        </h2>
      </Col>
    );
  }

  renderSub() {
    const { priceSummaryData } = this.props;

    if (!priceSummaryData.sub) {
      return null;
    }

    return (
      <Fragment>
        {
          _.map(priceSummaryData.sub, (sub, index) => ((
            <Col key={`price-summary-sub-${index}`} className="price-item-container" xs={12}>
              <Row noGutters>
                <Col xs={12}>
                  <p className="dark-text">
                    {sub.title && <span>{sub.title}</span>}
                    {sub.premium && <span>${numberWithCommas(sub.premium)}</span>}
                  </p>
                </Col>
                <MoreCollapse
                  title={sub.title}
                  titleSub={`${sub.count} pax`}
                  moreData={sub.details}
                />
              </Row>
            </Col>
          )))
        }
        <Col xs={12}><hr /></Col>
      </Fragment>
    );
  }

  renderPrice(priceData, isTotal, bigValueArray = [], isPromo = false) {
    const {
      promoCode,
      postCheckout,
      isCheckout,
      partialFlowStatus,
    } = this.props;

    if (!priceData) {
      return null;
    }

    try {
      let priceDataToUse = priceData;

      const isPromoFieldDisplay = !partialFlowStatus && isCheckout;

      if (isPromoFieldDisplay && isPromo) {
        const isPromoExists = priceData && _.find(priceData, data => data.from === 'promo_code');
        priceDataToUse = isPromoExists ? priceData : [...(priceData || []), { from: 'promo_code' }];
      }

      if (!isPromoFieldDisplay && isPromo && _.isEmpty(priceDataToUse)) {
        return null;
      }

      const isOnlyPromoApplied = priceDataToUse && priceDataToUse.length === 1 && priceDataToUse[0].from === 'promo_code' && priceDataToUse[0].value;

      return (
        <Fragment>
          <Col className="total-container" xs={12}>
            <Row noGutters>
              {
                isPromo &&
                <Col
                  className={['price-item-container', 'discount-label', !isOnlyPromoApplied || !isPromoFieldDisplay ? 'discount-label-without-promo' : ''].join(' ')}
                  xs={12}
                >
                  <Row noGutters>
                    <Col className="left-price" md={6}>
                      <p className="dark-text">
                        <span>Discounts</span>
                      </p>
                    </Col>
                  </Row>
                </Col>
              }
              {
                _.map(priceDataToUse, (price, index) => {
                  const bigValueStyle = price.title && _.find(bigValueArray, v => v === price.title) && 'big-value';
                  return (
                    <Col
                      className="price-item-container"
                      key={`price-summary-price-${isTotal ? 'total' : 'normal'}-${index}`}
                      xs={12}
                    >
                      <Row noGutters>
                        <Col className="d-none d-md-flex left-price" md={6}>
                          {
                            isPromoFieldDisplay && promoCode && price.from === 'promo_code' &&
                            <PromoCode
                              value={promoCode}
                              name="promo_code"
                              placeholderString="Promo Code"
                              postCheckout={promo => postCheckout(promo, false)}
                            />
                          }
                        </Col>
                        <Col className={price.from === 'promo_code' ? 'd-flex' : ''} xs={12} md={6}>
                          {
                            isPromoFieldDisplay && !promoCode && price.from === 'promo_code' ?
                              <div className="d-none d-md-block w-100">
                                <PromoCode
                                  value={promoCode}
                                  name="promo_code"
                                  placeholderString="Promo Code"
                                  postCheckout={promo => postCheckout(promo, false)}
                                />
                              </div>
                            :
                              <Row className="w-100" noGutters>
                                <Col className="price-item" xs={12}>
                                  <p className="dark-text">
                                    {price.title && <span>{price.title}</span>}
                                    {price.value && <span className={bigValueStyle}>{price.value}</span>}
                                  </p>
                                </Col>
                                <Col xs={12}>
                                  <p className="grey-text sub_text">
                                    {price.title_sub && <span>{price.title_sub}</span>}
                                  </p>
                                </Col>
                              </Row>
                          }
                        </Col>
                      </Row>
                    </Col>
                  );
                })
              }
              {
                isPromo && isPromoFieldDisplay &&
                <Col
                  className="price-item-container d-flex d-md-none"
                  xs={12}
                >
                  <PromoCode
                    value={promoCode}
                    name="promo_code"
                    placeholderString="Promo Code"
                    postCheckout={promo => postCheckout(promo, false)}
                  />
                </Col>
              }
            </Row>
          </Col>
          {!isTotal && <Col xs={12}><hr /></Col>}
        </Fragment>
      );
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }

  renderPromoCode() {
    const { priceSummaryData } = this.props;

    return this.renderPrice(priceSummaryData.promo, false, null, true);
  }

  renderPayDeposit() {
    const { isCheckout, partialFlowStatus } = this.props;

    if (partialFlowStatus || isCheckout) {
      return null;
    }

    return (
      <Col xs={12}>
        <div className="pay-deposit-container">
          <p>Pay a 30% deposit now and get immediate provisional cover!</p>
        </div>
      </Col>
    );
  }

  renderTnC() {
    const { values, errors } = this.state;
    const { priceSummaryData, isCheckout, partialFlowStatus } = this.props;

    if (partialFlowStatus === 'paid' || !isCheckout) {
      return null;
    }

    return (
      <Fragment>
        <Col xs={12}>
          {
            _.map(priceSummaryData.wordings, (wording, index) => {
              const name = `tnc_${index}`;
              return (
                <Checkbox
                  key={name}
                  value={values[name] && values[name].value}
                  name={name}
                  desc={wording}
                  onChange={this.handleOnChange}
                  errorString={errors[name]}
                  isRequired
                />
              );
            })
          }
        </Col>
      </Fragment>
    );
  }

  renderButton() {
    const { buttonText, partialFlowStatus } = this.props;

    if (partialFlowStatus === 'paid') {
      return null;
    }

    return (
      <Col className="button-container" xs={12}>
        <Button
          variant="medium-flat-green"
          height={48}
          width={205}
          onClick={this.handleOnNext}
        >
          <p>{buttonText}</p>
        </Button>
      </Col>
    );
  }

  render() {
    const { priceSummaryData } = this.props;

    if (!priceSummaryData) {
      return null;
    }

    return (
      <Col xs={12} className="checkout-price-summary-container">
        <Row noGutters>
          {this.renderMain()}
          {this.renderSub()}
          {this.renderPrice(priceSummaryData.price, false)}
          {this.renderPromoCode()}
          {this.renderPrice(priceSummaryData.total, true, ['Total', 'Balance'])}
          {this.renderTnC()}
          {this.renderPayDeposit()}
          {this.renderButton()}
        </Row>
      </Col>
    );
  }
}

CheckoutPriceSummary.defaultProps = {
  promoCode: null,
  partialFlowStatus: null,
};

CheckoutPriceSummary.propTypes = {
  priceSummaryData: PropTypes.object.isRequired,
  isCheckout: PropTypes.bool.isRequired,
  buttonText: PropTypes.string.isRequired,
  onNext: PropTypes.func.isRequired,
  postCheckout: PropTypes.func.isRequired,
  promoCode: PropTypes.string,
  partialFlowStatus: PropTypes.string,
};

export default CheckoutPriceSummary;
