import React, { Component } from 'react';
import { Row, Col } from 'reactstrap';
import PropTypes from 'prop-types';

import Logos from 'assets/images/InsurerLogos';

import './CheckoutHeader.scss';

class CheckoutHeader extends Component {
  render() {
    const { headerData } = this.props;

    try {
      const logo = Logos.square[headerData.insurer_slug];

      return (
        <Col xs={12}>
          <Row className="checkout-header-container" noGutters>
            <Col className="insurer-container" xs={12}>
              <div className="insurer-logo">
                { logo && <img src={logo} alt={`${headerData.insurer_slug}.png`} /> }
              </div>
              { headerData.title && <h2>{headerData.title}</h2> }
            </Col>
            <Col xs={12}>
              { headerData.footnote && <p className="grey-text">{headerData.footnote}</p> }
            </Col>
            <Col xs={12}>
              <hr />
            </Col>
          </Row>
        </Col>
      );
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }
}

CheckoutHeader.propTypes = {
  headerData: PropTypes.object.isRequired,
};

export default CheckoutHeader;
