import React, { Component } from 'react';
import { Row, Col } from 'reactstrap';
import ProceedImg from 'assets/images/Business/Checkout/Proceed.svg';

import './CheckoutBanner.scss';

class CheckoutBanner extends Component {
  render() {
    return (
      <Row noGutters className="business-checkout-banner right-container">
        <Col className="business-checkout-banner-inner" xs={12}>
          <h2 className="proceed-text">Proceed to get instant cover!</h2>
          <div className="proceed-image-container">
            <img src={ProceedImg} alt="Proceed.svg" />
          </div>
        </Col>
      </Row>
    );
  }
}

export default CheckoutBanner;
