import $ from 'jquery';
import window from 'global/window';
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import {
  Row,
  Col,
  Button,
} from 'reactstrap';

import Sticky from 'components/Templates/Sticky/Sticky';
import FormLandingFooter from 'components/Templates/FormLanding/FormLandingFooter';
import ButtonCustom from 'components/FormsV3/Button/Button';

import { numberWithCommas } from 'lib/helperFunctions';

import PriceBottom from './PriceBottom/PriceBottom';

import './BusinessFlow.scss';

class BusinessFlow extends Component {
  componentDidMount() {
    if ($('#intercom-container')) {
      $('#intercom-container').hide();
    }
  }

  componentWillUnmount() {
    if ($('#intercom-container')) {
      $('#intercom-container').show();
    }
  }

  getStickyPrice() {
    const { stickyInfo } = this.props;

    const isDeposit = stickyInfo && stickyInfo.isDeposit;
    const isBalance = stickyInfo && stickyInfo.isBalance;
    const quote = stickyInfo && stickyInfo.quote && stickyInfo.quote.quote;

    // eslint-disable-next-line
    const stickyPrice = quote && (
      // eslint-disable-next-line
      isBalance ? quote.outstanding_premium : 
      // eslint-disable-next-line
      isDeposit ? quote.deposit_premium : quote.final_premium );
      //   // eslint-disable-next-line
      // isCheckout ? quote.final_premium : quote.final_premium_no_gst);

    return {
      stickyPrice,
      desc: isBalance ? 'Balance' : 'Total price',
      totalCount: quote && quote.total_count,
    };
  }

  renderTopFlow(isSticky) {
    const {
      onBack,
      onNext,
      title,
      headerImage,
    } = this.props;

    const priceInfo = this.getStickyPrice();

    return (
      <Row className={isSticky ? 'sticky-top-container' : 'top-container'} noGutters>
        <Col className="flow-back" xs={3}>
          {
            onBack &&
            <Button
              onClick={onBack}
              color="link"
            >
              <h6>Back</h6>
            </Button>
          }
        </Col>
        <Col className="flow-title" xs={6}>
          {
            !isSticky && headerImage &&
              <div className="title-img">
                <img src={headerImage} alt="flow-img.svg" />
              </div>
          }
          {
            title && <h3>{title}</h3>
          }
        </Col>
        <Col className="flow-next" xs={3}>
          {
            isSticky && priceInfo && priceInfo.stickyPrice &&
              <div className="sticky-top-price">
                <p>{priceInfo.desc}</p>
                <p>${numberWithCommas(priceInfo.stickyPrice)}</p>
              </div>
          }
          {
            onNext &&
            <ButtonCustom
              variant="medium-flat-green"
              height={48}
              width={168}
              onClick={onNext}
            >
              <p>Next</p>
            </ButtonCustom>
          }
        </Col>
      </Row>
    );
  }

  render() {
    const {
      noFooter,
      noPolicyPal,
      usingSticky,
      transitionClass,
      stickyInfo,
    } = this.props;

    const info = stickyInfo && stickyInfo.quote && stickyInfo.quote.info;
    const priceInfo = this.getStickyPrice();

    return (
      <div className="business-flow-container" >
        <div className={['business-flow-inner-container', transitionClass].join(' ')}>
          <Row noGutters>
            <Col xs={12}>
              {this.renderTopFlow(false)}
            </Col>
            <Col xs={12}>
              {this.props.children}
            </Col>
          </Row>
          <Sticky isTop>
            {this.renderTopFlow(true)}
          </Sticky>
          {
            stickyInfo !== null && priceInfo !== null &&
            <div className="d-block d-lg-none">
              <Sticky>
                <PriceBottom
                  data={priceInfo && priceInfo.stickyPrice && {
                    productName: info && info.insurer_name,
                    productInfo: `${info && info.product_name} - ${priceInfo.totalCount} pax`,
                    price: `$${numberWithCommas(priceInfo.stickyPrice)}`,
                  }}
                />
              </Sticky>
            </div>
          }
        </div>
        {
          !noFooter ?
            <FormLandingFooter
              noPolicyPal={noPolicyPal}
              usingSticky={usingSticky}
            />
          : null
        }
      </div>
    );
  }
}

BusinessFlow.defaultProps = {
  title: '',
  onNext: null,
  onBack: null,
  noFooter: false,
  noPolicyPal: true,
  usingSticky: false,
  headerImage: null,
  transitionClass: '',
  // stickyPrice: null,
  stickyInfo: null,
  // isCheckout: false,
};

BusinessFlow.propTypes = {
  title: PropTypes.string,
  onNext: PropTypes.func,
  onBack: PropTypes.func,
  noFooter: PropTypes.bool,
  noPolicyPal: PropTypes.bool,
  usingSticky: PropTypes.bool,
  headerImage: PropTypes.string,
  transitionClass: PropTypes.string,
  // stickyPrice: PropTypes.number,
  stickyInfo: PropTypes.object,
  // isCheckout: PropTypes.bool,
};

export default BusinessFlow;
