import React, { Component } from 'react';
import { CustomInput } from 'reactstrap';
import PropTypes from 'prop-types';
import FormInputWrapper from '../FormInputWrapper/FormInputWrapper';

import './Checkbox.scss';

export default class Checkbox extends Component {
  render() {
    const {
      name,
      value,
      desc,
      onChange,
      errorString,
      extraInfo,
    } = this.props;

    return (
      <FormInputWrapper
        errorString={errorString}
        extraInfo={extraInfo}
        smallMargin
      >
        <div className="checkbox-container-v3">
          <CustomInput
            id={name}
            className="checkbox-input"
            type="checkbox"
            onChange={() => onChange('checkbox', name, !value)}
            checked={!!value}
          />
          {/* eslint-disable-next-line */}
          <p
            className="checkbox-input-desc"
            // onClick={() => onChange('checkbox', name, !value)}
          >
            {desc}
          </p>
        </div>
      </FormInputWrapper>
    );
  }
}

Checkbox.defaultProps = {
  value: null,
  errorString: null,
  extraInfo: null,
};

Checkbox.propTypes = {
  value: PropTypes.bool,
  name: PropTypes.string.isRequired,
  desc: PropTypes.oneOfType([PropTypes.string, PropTypes.object, PropTypes.array]).isRequired,
  onChange: PropTypes.func.isRequired,
  errorString: PropTypes.string,
  extraInfo: PropTypes.object,
};
