import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import { Row, Col } from 'reactstrap';
import PropTypes from 'prop-types';

import Arrow from 'assets/images/forms/arrow.svg';
import { numberWithCommas } from 'lib/helperFunctions';

import DetailCollapse from 'components/Templates/DetailCollapse/DetailCollapse';

import './MoreCollapse.scss';

class MoreCollapse extends Component {
  constructor(props) {
    super(props);

    this.state = {
      collapse: false,
    };
  }

  render() {
    const { collapse } = this.state;
    const { title, titleSub, moreData } = this.props;

    return (
      <Fragment>
        <Col xs={12}>
          <p className="grey-text">
            {titleSub && <span>{titleSub}</span>}
            {
              moreData &&
              <span>
                <button
                  className="more-button"
                  onClick={() => this.setState({ collapse: !collapse })}
                >
                  <p className="grey-text">
                    More
                    <img className={collapse ? 'rotate-arrow' : ''} src={Arrow} alt="Arrow.svg" />
                  </p>
                </button>
              </span>
            }
          </p>
        </Col>
        {
          moreData &&
          <DetailCollapse
            className="checkout-collapse-more-container"
            collapseOverwrite={collapse}
            useOverwrite
          >
            <Col xs={12} className="more-collapse-container">
              <Row noGutters>
                {
                  _.map(moreData, (data, index) => (
                    <Col key={`${title}-more-${index}`} xs={12}>
                      <Row noGutters>
                        <Col xs={4}><p className="grey-text">{data.title || 'N/A'}</p></Col>
                        <Col xs={4}><p className="grey-text">{data.count ? `${data.count} pax` : 'N/A'}</p></Col>
                        <Col xs={4}><p className="grey-text">{data.premium ? `$${numberWithCommas(data.premium)}` : 'N/A'}</p></Col>
                      </Row>
                    </Col>
                  ))
                }
              </Row>
            </Col>
          </DetailCollapse>
        }
      </Fragment>
    );
  }
}

MoreCollapse.defaultProps = {
  titleSub: null,
  moreData: null,
};

MoreCollapse.propTypes = {
  title: PropTypes.string.isRequired,
  titleSub: PropTypes.string,
  moreData: PropTypes.array,
};

export default MoreCollapse;
