import window from 'global/window';
import React, { Component } from 'react';
import { Row, Col } from 'reactstrap';

import PropTypes from 'prop-types';

import Button from 'components/FormsV3/Button/Button';
import ModalPopup from 'components/Templates/ModalPopup/ModalPopup';
import StripePayment from 'components/Templates/StripePayment/StripePayment';
import CheckoutComplete from 'components/Templates/CheckoutPopup/CheckoutComplete';

import Axios from 'lib/axiosFunctions';
import { numberWithCommas } from 'lib/helperFunctions';
import { postStripe } from 'actions/insuranceActions';

import './CheckoutPayment.scss';

const paymentFailedError = 'Payment failed! Please try again.';

class CheckoutPayment extends Component {
  constructor(props) {
    super(props);

    this.state = {
      stripeMarkup: null,
      errorString: null,
    };

    this.handleFiatPayment = this.handleFiatPayment.bind(this);
    this.handleStripe = this.handleStripe.bind(this);
    this.hideModal = this.hideModal.bind(this);
  }

  async componentDidMount() {
    const {
      paymentIntent,
      checkOtp,
      postCheckout,
      promoCode,
      setStateToSubmitted,
    } = this.props;

    if (paymentIntent && checkOtp) {
      const result = await checkOtp();
      if (result) {
        setTimeout(() => {
          if (setStateToSubmitted) {
            setStateToSubmitted();
          }
          this.checkoutComplete.openCheckoutComplete();
        }, 300);
      } else {
        await postCheckout(promoCode, true);
        this.setState({
          errorString: paymentFailedError,
          stripeMarkup: null,
        });
      }
    }
  }

  hideModal() {
    const { hideModal } = this.props;

    this.setState({
      stripeMarkup: null,
      errorString: null,
    });
    hideModal();
  }

  async handleStripe() {
    const {
      checkoutData,
      bankTransferData,
      handleAPI,
      partialFlowStatus,
    } = this.props;

    try {
      this.setState({
        stripeMarkup: null,
        postAddress: null,
        errorString: null,
      }, async () => {
        if (checkoutData && checkoutData.payment_template_links) {
          // eslint-disable-next-line
          const url = partialFlowStatus ? checkoutData.payment_template_links.outstanding : bankTransferData.is_full ? checkoutData.payment_template_links.full_payment : checkoutData.payment_template_links.deposit;
          const result = handleAPI ? await handleAPI(() => Axios.GET(url)) : await Axios.GET(url);

          if (result && result.status === 200 && result.data) {
            try {
              const postAddress = result.data.split('action="')[1].split('"')[0];

              this.setState({
                stripeMarkup: result.data,
                postAddress,
              }, () => {
                this.stripePayment.invokeClick(() => {}, result.data);
              });

              return;
            } catch (error) {
              // esline-disable-next-line
            }
          }
        }

        this.setState({
          errorString: paymentFailedError,
          stripeMarkup: null,
        });
      });
    } catch (error) {
      // eslint-disable-next-line
    }
  }

  async handleFiatPayment(result) {
    const { postAddress } = this.state;
    const { handleAPI, setStateToSubmitted } = this.props;

    if (postAddress) {
      const stripeResult = handleAPI ? await handleAPI(() => postStripe(result, postAddress)) : await postStripe(result, postAddress);

      if (stripeResult && stripeResult.status === 'requires_action' && stripeResult.redirect_url) {
        window.location.href = stripeResult.redirect_url;
        handleAPI(() => new Promise((resolve) => { setTimeout(() => { resolve(true); }, 10000); }));
        return;
      }

      if (stripeResult && stripeResult.charged) {
        setTimeout(() => {
          if (setStateToSubmitted) {
            setStateToSubmitted();
          }
          this.checkoutComplete.openCheckoutComplete();
        }, 300);

        return;
      }
    }

    this.setState({
      errorString: paymentFailedError,
      stripeMarkup: null,
    });
  }

  renderBody() {
    const {
      checkoutData,
      setPaymentView,
      bankTransferData,
      partialFlowStatus,
    } = this.props;

    if (!checkoutData) {
      return null;
    }

    try {
      const price = checkoutData && checkoutData.quote;
      // eslint-disable-next-line
      const finalPrice = partialFlowStatus ? price.outstanding_premium : bankTransferData.is_full ? price.final_premium : price.deposit_premium;
      const premiumDesc = partialFlowStatus ? 'Pay balance' : `${bankTransferData.is_full ? '100%' : `${price.deposit_percent}%`} of premium`;

      return (
        <Row>
          {
            !partialFlowStatus &&
            <Col xs={12}>
              <div className="method-container">
                <button
                  className={`method-button ${bankTransferData.is_full ? '' : 'selected'}`}
                  onClick={() => setPaymentView(false, false)}
                >
                  Deposit
                </button>
                <button
                  className={`method-button ${!bankTransferData.is_full ? '' : 'selected'}`}
                  onClick={() => setPaymentView(false, true)}
                >
                  Full payment
                </button>
              </div>
            </Col>
          }
          <Col xs={12}>
            {
              !partialFlowStatus && !bankTransferData.is_full &&
              <div className="pay-deposit-container">
                <p>Pay a {price.deposit_percent}% deposit now and get immediate provisional cover!</p>
              </div>
            }
            <p className="price-container">
              <span>{premiumDesc}</span>
              <span>${numberWithCommas(finalPrice)}</span>
            </p>
          </Col>
          <Col xs={12}>
            <hr />
          </Col>
        </Row>
      );
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }

  renderFooter() {
    const { stripeMarkup, errorString } = this.state;
    const {
      setPaymentView,
      bankTransferData,
      checkoutData,
      partialFlowStatus,
    } = this.props;

    const fees = checkoutData && checkoutData.quote && (bankTransferData.is_full || partialFlowStatus ? checkoutData.quote.fees : checkoutData.quote.deposit_fees);

    try {
      return (
        <Row className="button-container" noGutters>
          {
            errorString &&
            <Col xs={12}>
              <p className="error-container">{errorString}</p>
            </Col>
          }
          <Col xs={12} sm={6}>
            <div className="cc-button">
              <Button
                variant="medium-outline"
                height={48}
                width={162}
                onClick={this.handleStripe}
              >
                <p>Pay via credit card</p>
              </Button>
              <p>*Additional 3% charge applies</p>
              {fees && <p className="fees">(${numberWithCommas(fees)})</p>}
            </div>
          </Col>
          <Col xs={12} sm={6}>
            <div className="bank-button">
              <Button
                variant="medium-flat-green"
                height={48}
                width={162}
                onClick={() => {
                  window.scrollTo(0, 0);
                  setPaymentView(true, bankTransferData.is_full);
                  this.hideModal();
                }}
              >
                <p>Pay via bank transfer</p>
              </Button>
            </div>
          </Col>
          <StripePayment
            ref={(node) => { this.stripePayment = node; }}
            markup={stripeMarkup}
            paymentCallback={this.handleFiatPayment}
          />
          <CheckoutComplete
            isQuoteOnly
            upperText="Submission Successful!"
            midText="Our friendly account manager will be in contact with you upon payment confirmation via email within the next work day!"
            buttonText="Back to Home"
            wrappedComponentRef={node => this.checkoutComplete = node}
          />
        </Row>
      );
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }

  render() {
    const { showModal } = this.props;

    return (
      <ModalPopup
        showModal={showModal}
        closeModal={this.hideModal}
        className="checkout-v2-payment-modal"
        backdropClassName="blue-modal-backdrop"
        header={<p>Payment method</p>}
        body={this.renderBody()}
        footer={this.renderFooter()}
      />
    );
  }
}

CheckoutPayment.defaultProps = {
  checkoutData: null,
  handleAPI: null,
  promoCode: null,
  partialFlowStatus: null,
};

CheckoutPayment.propTypes = {
  showModal: PropTypes.bool.isRequired,
  hideModal: PropTypes.func.isRequired,
  setPaymentView: PropTypes.func.isRequired,
  checkoutData: PropTypes.object,
  bankTransferData: PropTypes.object.isRequired,
  handleAPI: PropTypes.func,
  postCheckout: PropTypes.func.isRequired,
  promoCode: PropTypes.string,
  partialFlowStatus: PropTypes.string,
  setStateToSubmitted: PropTypes.func.isRequired,
};

export default CheckoutPayment;
