/**
 * PromoCode.js
 * PromoCode field that takes in a callback
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import React, { Component } from 'react';
import { Input } from 'reactstrap';
import PropTypes from 'prop-types';

import Button from 'components/FormsV3/Button/Button';
import FormInputWrapper from '../FormInputWrapper/FormInputWrapper';

import '../TextInput/TextInput.scss';
import './PromoCode.scss';

const defaultHeight = 48;

/**
 * Custom Form Dropdown class
 */
class PromoCode extends Component {
  static getDerivedStateFromProps(nextProps, prevState) {
    const { value } = nextProps;
    const { oldValue } = prevState;

    if (value !== oldValue) {
      return {
        value,
        oldValue: value,
      };
    }

    return null;
  }

  constructor(props) {
    super(props);

    const { value } = this.props;

    this.state = {
      value,
      oldValue: value,
      errorString: null,
    };
  }

  componentDidMount() {
    this.isComMounted = true;
  }

  componentWillUnmount() {
    this.isComMounted = false;
  }

  render() {
    const { value, errorString, oldValue } = this.state;
    const { value: propsValue } = this.props;

    const {
      name,
      placeholderString,
      extraInfo,
      postCheckout,
    } = this.props;

    const buttonText = propsValue ? 'Remove' : 'Apply';

    return (
      <FormInputWrapper
        extraInfo={extraInfo}
        errorString={errorString}
      >
        <div
          className="text-input-container-v3 promo-container-v3"
          style={{ height: defaultHeight }}
        >
          <div className="text-input ">
            <Input
              className="text"
              placeholder={placeholderString}
              name={name}
              disabled={propsValue}
              value={value || ''}
              onChange={e => this.setState({ value: e.target.value })}
            />
            <div className="promo-button">
              <Button
                variant="medium-outline-edit"
                height={32}
                width={88}
                onClick={async () => {
                  try {
                    if (propsValue) {
                      await postCheckout(null);

                      if (this.isComMounted) {
                        this.setState({
                          errorString: null,
                        });
                      }
                      return;
                    }

                    if (value) {
                      const result = await postCheckout(value);
                      if (this.isComMounted) {
                        // Promo code will not be set
                        if (!result) {
                          this.setState({
                            errorString: 'Invalid promo code!',
                            value: oldValue,
                          });
                        } else {
                          this.setState({
                            errorString: null,
                          });
                        }
                      }
                    }
                  } catch (error) {
                    // eslint-disable-next-line
                  }
                }}
              >
                <p>{buttonText}</p>
              </Button>
            </div>
          </div>
        </div>
      </FormInputWrapper>
    );
  }
}

PromoCode.defaultProps = {
  extraInfo: null,
  value: '',
};


PromoCode.propTypes = {
  placeholderString: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  postCheckout: PropTypes.func.isRequired,
  extraInfo: PropTypes.object,
  value: PropTypes.string,
};

export default PromoCode;
