import window from 'global/window';
import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';

import './Sticky.scss';

class Sticky extends Component {
  constructor(props) {
    super(props);

    this.state = {
      showSticky: false,
    };

    this.onMovement = this.onMovement.bind(this);
  }

  componentDidMount() {
    window.addEventListener('scroll', this.onMovement);
    window.addEventListener('resize', this.onMovement);

    this.onMovement();
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.onMovement);
    window.removeEventListener('resize', this.onMovement);
  }

  onMovement() {
    if (this.node) {
      const { showSticky } = this.state;

      const { scrollY } = window;
      const isResp = window.innerWidth < 992 && !showSticky;
      const height = isResp ? 150 : 100;

      if (
        (scrollY < height && showSticky) ||
        (scrollY >= height && !showSticky)
      ) {
        this.setState({
          showSticky: scrollY >= height,
        });
      }
    }
  }

  render() {
    const { showSticky } = this.state;

    const { isTop } = this.props;
    const stickyStyle = isTop ? 'top-sticky' : 'bottom-sticky';
    let showStyle = '';
    if (showSticky) {
      showStyle = isTop ? 'show-top-sticky' : 'show-bottom-sticky';
    }

    return (
      <Fragment>
        <div
          ref={node => this.node = node}
          className={`business-sticky ${stickyStyle} ${showStyle}`}
        >
          <div className="sticky-inner">
            {this.props.children}
          </div>
        </div>
      </Fragment>
    );
  }
}

Sticky.defaultProps = {
  isTop: false,
};

Sticky.propTypes = {
  isTop: PropTypes.bool,
};

export default Sticky;
