import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Col, Collapse, Button } from 'reactstrap';

import Arrow from 'assets/images/forms/arrow.svg';

import './DetailCollapse.scss';

class DetailCollapse extends Component {
  constructor(props) {
    super(props);
    this.toggle = this.toggle.bind(this);
    this.state = { collapse: false };
  }

  toggle() {
    this.setState({ collapse: !this.state.collapse });
  }

  render() {
    const { collapse } = this.state;
    const {
      collapseOverwrite,
      useOverwrite,
      className,
      topHr,
    } = this.props;

    const collapseToUse = useOverwrite ? collapseOverwrite : collapse;

    return (
      <Col className={['detail-collapse-container', className].join(' ')} xs={12}>
        {
          !useOverwrite &&
          <Button
            onClick={this.toggle}
            className="collapse-button"
          >
            <p className="grey-text collapse-text">{collapse ? 'Hide details' : 'More details'}</p>
            <img className={collapse ? 'rotate-arrow' : ''} src={Arrow} alt="Arrow.svg" />
          </Button>
        }
        {!useOverwrite && collapseToUse && topHr && <hr />}
        <Collapse isOpen={collapseToUse}>
          {this.props.children}
        </Collapse>
        {!useOverwrite && !topHr && <hr />}
      </Col>
    );
  }
}

DetailCollapse.defaultProps = {
  collapseOverwrite: false,
  useOverwrite: false,
  className: '',
  topHr: false,
};

DetailCollapse.propTypes = {
  collapseOverwrite: PropTypes.bool,
  useOverwrite: PropTypes.bool,
  topHr: PropTypes.bool,
  className: PropTypes.string,
};

export default DetailCollapse;
