// @flow
import $ from 'jquery';
import document from 'global/document';
import window from 'global/window';
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import LoadingScreenV2 from 'components/Templates/LoadingScreenV2/LoadingScreenV2';

class StripePayment extends Component {
  constructor(props) {
    super(props);

    this.state = {
      currMarkup: null,
      extraData: null,
      isLoading: false,
      isLoadingError: false,
    };

    this.cleanCheckoutButtonContainer = this.cleanCheckoutButtonContainer.bind(this);
    this.setNewMarkup = this.setNewMarkup.bind(this);
    this.invokeClick = this.invokeClick.bind(this);

    this.showLoading = this.showLoading.bind(this);
    this.showLoadingError = this.showLoadingError.bind(this);
  }

  componentDidMount() {
    const { markup } = this.props;
    this.setNewMarkup(markup);
  }

  componentDidUpdate() {
    const { currMarkup } = this.state;
    const { markup } = this.props;

    if (currMarkup !== markup) {
      if (markup) {
        this.setNewMarkup(markup);
      } else if (!markup) {
        this.setState({
          currMarkup: null,
        });
      }
    }
  }

  setNewMarkup(markup) {
    const { paymentCallback, replacementIDString } = this.props;

    if (!markup) {
      return;
    }

    this.cleanCheckoutButtonContainer();

    const range = document.createRange();
    if (replacementIDString) {
      this.checkoutButton.appendChild(range.createContextualFragment(_.replace(markup, replacementIDString, 'id="checkout_form"')));
    } else {
      this.checkoutButton.appendChild(range.createContextualFragment(markup));
    }

    $('#checkout_form').get(0).submit = () => {
      const { extraData } = this.state;
      const data = $('#checkout_form').serializeArray();


      paymentCallback(data, extraData);

      this.setState({
        extraData: null,
      });

      // Prevent form submit.
      return false;
    };

    // eslint-disable-next-line
    this.setState({
      currMarkup: markup,
    });
  }

  showLoading(isLoading) {
    this.setState({ isLoading });
  }

  showLoadingError(isLoadingError) {
    this.setState({ isLoadingError });
  }

  cleanCheckoutButtonContainer() {
    while (this.checkoutButton.firstChild) {
      this.checkoutButton.removeChild(this.checkoutButton.firstChild);
    }
  }

  invokeClick(failCallback, extraData, doTag = null) {
    const { zeroPaymentCallback } = this.props;

    let failCallTimeout = null;

    this.setState({
      extraData,
    });

    this.showLoading(true);

    const ele = document.getElementsByName('stripe_checkout_app');
    for (let i = ele.length - 1; i >= 0; i--) {
      ele[i].parentNode.removeChild(ele[i]);
    }

    const invokeClick = setInterval(() => {
      try {
        const { amount } = $('#checkout_form')[0].getElementsByClassName('stripe-button')[0].dataset;
        if (amount === '0') {
          this.showLoading(false);
          if (doTag) {
            doTag();
          }
          if (zeroPaymentCallback) {
            zeroPaymentCallback();
          }
          clearInterval(invokeClick);
          clearTimeout(failCallTimeout);
          return;
        }
      } catch (error) {
        console.log('Get new payable failed');
        clearInterval(invokeClick);
        clearTimeout(failCallTimeout);
        this.showLoadingError(true);
        failCallback();
      }

      try {
        const button = $('#checkout_form')[0].getElementsByClassName('stripe-button-el')[0];
        if (button) {
          $(button).trigger('click');
          if (doTag) {
            doTag();
          }
          clearInterval(invokeClick);
          clearTimeout(failCallTimeout);
          setTimeout(() => this.showLoading(false), 5000);
        }
      } catch (error) {
        console.log('Button not yet generated!');
        clearInterval(invokeClick);
        clearTimeout(failCallTimeout);
        this.showLoadingError(true);
        failCallback();
      }
    }, 10);

    failCallTimeout = setTimeout(() => {
      clearInterval(invokeClick);
      this.showLoadingError(true);
      failCallback();
    }, 10000);
  }

  render() {
    const { isLoading, isLoadingError } = this.state;

    return (
      <Fragment>
        <div className="d-none" ref={(node) => { this.checkoutButton = node; }} />
        <LoadingScreenV2
          isLoading={isLoading}
          isLoadingError={isLoadingError}
          message={<h5>Please try again.</h5>}
          showLoading={this.showLoading}
          showLoadingError={this.showLoadingError}
          noLogos
        />
      </Fragment>
    );
  }
}

StripePayment.defaultProps = {
  markup: null,
  replacementIDString: null,
  zeroPaymentCallback: null,
};

StripePayment.propTypes = {
  markup: PropTypes.string,
  // doLoadStripeState: PropTypes.func.isRequired,
  paymentCallback: PropTypes.func.isRequired,
  zeroPaymentCallback: PropTypes.func,
  replacementIDString: PropTypes.string,
};

export default StripePayment;
